@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap";
:root {
  --blue-color: #007bff;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-inline: 50px;
  display: flex;
}

.header .logo {
  width: 40px;
  height: 40px;
  margin-top: 3px;
}

.nav {
  align-items: center;
  display: flex;
}

.header .nav .nav__list {
  padding: 0;
  list-style: none;
  display: flex;
}

.header .nav .nav__list .nav-item {
  display: flex;
}

.header .nav .nav__list .nav-item .nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.header .nav .nav__list .nav-item .nav__link.is-active {
  color: #00acdc;
  margin: 0;
  position: relative;
}

.header .nav .nav__list .nav-item .nav__link[data-qa="hover"]:hover {
  color: #00acdc;
}

.header .nav .nav__list .nav-item .nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 105%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (width <= 1024px) {
  .header .nav {
    margin: 0;
    padding: 0;
  }

  .header .nav .nav__list .nav-item .nav__link {
    margin-left: 20px;
  }
}
